import React from "react";
import InputBase from "@mui/material/InputBase";
import Button from "@mui/material/Button";
import ListItem from "@mui/material/ListItem";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import DiaLog from "./DiaLog";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import AlertMsg from "./AlertMsg";

export default function AddToForm({setInputText, todos, setTodos, inputText,}) {
  const [open, setOpen] = React.useState(false);
  const [openAlert, setOpenAlert] = React.useState(false);
  
  window.onload = ()=> {
    document.querySelector("#inputText").focus();
  }
  const inputTextHandler = (e) => {
    setInputText(e.target.value);
    // console.log(e.target.value)
  };
  const submitHandler = (e) => {
    e.preventDefault();
    if (document.querySelector("#inputText").value) {
      setTodos([
        ...todos,
        { text: inputText, completed: false, id: Math.random().toString(36).substring("3", "9")},
      ]);
      setInputText("");
    } else {
      setOpenAlert(true)
    }
  };

  const openDialog = ()=> {
    setOpen(true)
  }
  return (
    <>
      <Grid xs={12} item>
        <Paper>
          <ListItem>
            <Box component={"form"} sx={{width:"100%", display:"flex"}} onSubmit={submitHandler}>
              <InputBase
                sx={{ ml: 1, flex: 1, width: 386 }}
                placeholder="Enter a task"
                inputProps={{ "aria-label": "Enter a task" }}
                onChange={inputTextHandler}
                value={inputText}
                id="inputText"
                autoComplete="off"
              />
              <Button type="submit" variant="contained" color="primary">
                Add
              </Button>
            </Box>
          </ListItem>
        </Paper>
      </Grid>
      <Grid xs={12} item sx={{ position:'absolute', bottom:15, left:0, right:0, paddingRight: "8px"}}>
        <Paper elevation={3}>
          <ListItem sx={{justifyContent:"space-between"}}>
            <Typography><b>Total Tasks: </b>{todos.length}</Typography>
            <Button type="submit" variant="contained" color="error" onClick={openDialog}>
              Remove All
            </Button>
          </ListItem>
        </Paper>
      </Grid>
      <DiaLog open={open} setOpen={setOpen} setTodos={setTodos}/>
      <AlertMsg openAlert={openAlert} setOpenAlert={setOpenAlert} />
    </>
  );
}
